@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Caption:wght@400;700&family=PT+Sans:ital,wght@0,400;0,700;1,400&display=swap);
:root {
  --font-family: "PT Sans Caption", sans-serif;

  --gradient-text: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #ae67fa -13.86%, #f49867 99.55%);

  --color-text: #25282a;

  --color-bg: #f2f2f2;
  --color-salmon: #ff8a71;
  --color-blue: #5ca5a5;
  --color-yellow: #fcc117;
  --color-red: #e40000;

  /* new */

  --primary-light: #8abdff;
  --primary: #6d5dfc;
  --primary-dark: #5b0eeb;

  --white: #ffffff;
  --greyLight-1: #e4ebf5;
  --greyLight-2: #c8d0e7;
  --greyLight-3: #bec8e4;
  --greyDark: #9baacf;

  /* $shadow: 0.3rem 0.3rem 0.6rem var(--greyLight-2),
    -0.2rem -0.2rem 0.5rem var(--white);
  $inner-shadow: inset 0.2rem 0.2rem 0.5rem var(--greyLight-2),
    inset -0.2rem -0.2rem 0.5rem var(--white); */

  /* 12px, 16px, 20px, 52px, 62px */
  --font-sml: 0.785rem;
  --font-reg: 1rem;
  --font-med: 1.25rem;
  --font-lg: 2rem;
  --font-xl: 3rem;
  --font-xxl: 3.975rem;
}

.hero {
  width: 100%;
  height: 100%;
}
.hero__wrapper {
  height: 100%;
  flex-direction: column;
}
.hero__name {
  text-align: center;
  opacity: 0;
  transform: translateY(1rem);
  animation: fadeInUp 1s ease forwards;
}
.hero__name p {
  margin-top: 0.3rem;
}
.hero__social {
  margin-top: 3rem;
  opacity: 0;
  transform: translateY(1rem);
  animation: fadeInUp 1.8s ease 1s forwards;
}
.hero__social--icon svg {
  font-size: 1.8rem;
  margin-right: 1.2rem;
}
.hero__social--icon svg path {
  transition: all 0.2s ease-in-out;
}
.hero__social--icon:hover.hero__social--icon svg path {
  fill: var(--color-blue);
}

/* Animations */

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media screen and (max-width: 599px) {
}

.about {
  width: 100%;
  height: 100%;
}
.about__wrapper {
  height: 100%;
  grid-gap: 2rem;
  gap: 2rem;
  justify-content: space-between;
  align-items: center;
}
.about__img {
  flex: 1 1;
  position: relative;
  width: 500px;
  height: 530px;
  background-position: center;
  background-size: 95%;
  background-repeat: no-repeat;
}
.about__greeting {
  position: absolute;
  top: 156px;
  font-size: var(--font-xl);
}
.about__greeting > h1 {
  position: absolute;
  opacity: 0;
}
.about__text {
  flex: 2 1;
}
.about__social {
  margin-top: 1.5rem;
  flex-direction: column;
  grid-gap: 2rem;
  gap: 2rem;
}

/* Animations */

@keyframes fadeInOut {
  0% {
    transform: translateY(-10px);
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  50% {
    opacity: 1;
    /* transform: translateY(0); */
  }
  70% {
    opacity: 1;
  }
  100% {
    /* transform: translateY(10px); */
    opacity: 0;
  }
}

@keyframes fadeIn {
  0% {
    transform: translateY(-10px);
    opacity: 0;
  }
  100% {
    transform: translateY(10px);
    opacity: 1;
  }
}

.about__greeting h1:nth-child(1) {
  animation: fadeInOut 0.6s ease-in 1.2s forwards;
}
.about__greeting h1:nth-child(2) {
  animation: fadeInOut 0.6s ease-in 1.9s forwards;
}
.about__greeting h1:nth-child(3) {
  animation: fadeInOut 0.6s ease-in 2.6s forwards;
}
.about__greeting h1:nth-child(4) {
  animation: fadeIn 0.6s ease-in 3.9s forwards;
}

@media screen and (max-width: 599px) {
  .about__wrapper {
    margin-top: 70px;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
  }
  .about__img {
    flex: 1 1;
    width: 50%;
    height: 100%;
    background-position: center;
    background-size: 95%;
    background-repeat: no-repeat;
  }
  .about__greeting {
    display: none;
  }
  .about__text {
    flex: 2 1;
    text-align: center;
  }
  .about__social {
    margin-top: 1rem;
    flex-direction: row;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: center;
  }
  .about__social .hero__social--icon svg {
    font-size: 1.6rem;
  }
}

@media (min-width: 600px) and (max-width: 899px) {
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  margin-bottom: 2rem;
  /* background-color: var(--color-blue); */
  box-shadow: -0.2rem -0.2rem 0.5rem var(--white);
  border-bottom: 2px solid var(--color-text);
  z-index: 10000;
}

.header__wrapper {
  width: 70%;
  margin: 0 auto;
}

.header a {
  font-weight: 500;
}
.header a:hover {
  opacity: 0.6;
}
.header a.active {
  color: var(--color-text);
  font-weight: 700;
  background: radial-gradient(
    circle at 51% 50%,
    rgba(108, 201, 201, 1) 8%,
    rgba(242, 242, 242, 1) 43%,
    rgba(242, 242, 242, 1) 88%
  );
  padding: 1rem 1rem;
  animation: dropDown 0.2s ease-out 0.2s forwards;
}

/* animations */

@keyframes dropDown {
  0% {
    opacity: 0.7;
  }
  20% {
    /* opacity: 0.2; */
  }
  50% {
    /* opacity: 0; */
  }
  70% {
    /* opacity: 0.2; */
  }
  100% {
    transform: translateY(calc(100vh - 56px));
    opacity: 1;
  }
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1.8rem 0;
  text-align: left;
  /* background-color: var(--color-blue); */
  border-top: 2px solid var(--color-text);
  z-index: 2000;
}

.works {
  height: 100%;
}
.works__wrapper {
  height: 100%;
  grid-gap: 2rem;
  gap: 2rem;
}
.works__buttons {
  flex: 1 1;
  flex-direction: column;
  grid-gap: 2rem;
  gap: 2rem;
}
.works__buttons button {
  width: 70%;
  background-color: transparent;
  font-size: 1.2rem;
  font-weight: 600;
  padding: 0.7em 1em;
  border: none;
  border: 0.5px solid rgba(32, 37, 41, 0.2);
  box-shadow: 1px 1px 11px 0px rgba(32, 37, 41, 0.2),
    1px 1px 11px 0px rgba(255, 255, 255, 0.8) inset;
  border-radius: 0.55em;
  cursor: pointer;
}

.works__buttons .mixitup-control-active {
  /* background: var(--color-blue); */
  border: none;
  box-shadow: 1px 1px 11px 0px rgba(32, 37, 41, 0.2),
    1px 1px 11px 0px rgba(255, 255, 255, 0.4) inset;
  color: var(--color-blue);
}
/* .works__buttons .mixitup-control-active {
  opacity: 1;
  background-color: var(--color-blue);
  border: 0.5px solid var(--color-bg);
} */

.works__content {
  flex: 3 1;
  grid-gap: 0.3rem;
  gap: 0.3rem;
  justify-content: space-between;
  align-items: flex-start;
}

.works__item {
  width: 30%;
  margin-bottom: 2rem;
  text-align: center;
}
.works__item div {
  width: 200px;
  height: 130px;
  background-position: center;
  background-size: 95%;
  background-repeat: no-repeat;
  border-radius: 5px;
  margin: 0 auto;
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
  box-shadow: 2px 2px 8px 0px rgba(34, 60, 80, 0.2);
  transition: all 0.2s ease-in-out;
}
.works__item:hover.works__item div {
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
}
.works__item p {
  font-size: var(--font-reg);
  text-transform: uppercase;
  margin-top: 0.6rem;
}

.works-mobile {
  width: 100%;
  height: 100%;
}
.works-mobile__content {
  width: 100%;
  height: 100%;
  padding: 0 1rem;
  flex-direction: column;
}
.works-mobile__img {
  width: 100%;
  margin-bottom: 3rem;
  text-align: center;
}
.works-mobile__img div {
  width: 100%;
  height: 30vh;
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  border-radius: 5px;
  box-shadow: 2px 2px 8px 0px rgba(34, 60, 80, 0.2);
}

.works-mobile__text p {
  font-size: var(--font-reg);
  text-transform: uppercase;
}
.works-mobile__btn {
  width: 20px;
  height: 20px;
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
}

.works-mobile__btn svg {
  fill: var(--color-blue);
}

.works-icon {
  color: var(--color-text);
}

.works-mobile__btn:last-child {
  text-align: right;
}

.projects {
  width: 100%;
  height: 100%;
}
.projects__wrapper {
  height: 100%;
}

@media (min-width: 600px) and (max-width: 899px) {
}

.cv {
  width: 100%;
  height: 100%;
}
.cv__wrapper {
  width: 100%;
  height: 100%;
}
iframe {
  border: none;
  width: 70%;
  height: 80%;
}

@media screen and (max-width: 599px) {
  iframe {
    border: none;
    width: 90%;
    height: 80%;
  }
}

/* global styles */

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  font-family: "PT Sans", sans-serif;
  background: var(--color-bg);
  margin: 0;
  padding: 0;
  font-size: var(--font-med);
  font-weight: 400;
  line-height: 1.3;
  color: var(--color-text);
  overflow: hidden;
  /* border: 2px solid red; */
}
.app {
  width: 100vw;
  height: 100vh;
}
@media screen and (max-width: 599px) {
  body {
    font-size: var(--font-reg);
  }
}

/* text */

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "PT Sans Caption", sans-serif;
  margin: 0;
  font-weight: 700;
}

a,
a:visited,
a:hover {
  color: unset;
  text-decoration: none;
}

/* spacing */

.container {
  max-width: 80.375rem;
  margin: 0 auto;
}
.container--pdall {
  padding-top: 4.375rem;
  padding-right: 1.5rem;
  padding-bottom: 4.375rem;
  padding-left: 1.5rem;
}
.container--pdY {
  padding-top: 4.375rem;
  padding-bottom: 4.375rem;
}
.container--pdX {
  padding-right: 1.5rem;
  padding-left: 1.5rem;
}

/* flexbox */

.flex {
  display: flex;
  flex-wrap: wrap;
}
.jc-sb {
  justify-content: space-between;
}
.jc-sa {
  justify-content: space-around;
}
.jc-c {
  justify-content: center;
}
.jc-fe {
  justify-content: flex-end;
}
.ai-c {
  align-items: center;
}
.ai-fs {
  align-items: flex-start;
}
.ai-fe {
  align-items: flex-end;
}

/* visibility */

@media screen and (max-width: 1050px) {
  .hide-for-desktop {
    display: none;
  }
}
@media screen and (max-width: 650px) {
  .hide-for-mobile {
    display: none;
  }
}

